body{background-color:#1c1c1c;font-family:'Signika', sans-serif}.content{display:flex;flex-direction:column;background-color:#1c1c1c;width:100%;padding:5vh;box-sizing:border-box;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.site-header{align-self:center;font-size:40px;color:white;margin-bottom:35px;-webkit-animation:fade-in 1200ms ease-in;animation:fade-in 1200ms ease-in}.top-row,.bottom-row{display:flex;justify-content:space-between;height:3vh;margin-bottom:35px}.top-row *,.bottom-row *{border-radius:7px}.left-top-row{display:flex;width:35%;justify-content:space-between}#season-dropdown,#stat-dropdown{width:45%;cursor:pointer;border:none;padding:0px 7px}.not-selected-error{background-color:#af2a2a}.not-selected-error.animation{animation:error-shake 400ms linear;-moz-animation:error-shake 400ms linear;-o-animation:error-shake 400ms linear;-webkit-animation:error-shake 400ms linear}.right-top-row{display:flex;width:5%;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}.glossary-button{width:100%;cursor:pointer;border:none;padding:0px 7px}.search-players-input-container{display:flex;flex-direction:column;width:19.5%;margin-right:7vw;position:relative}#search-players-input{min-height:100%;box-sizing:border-box;border:none;padding:10px}#player-dropdown{background-color:grey;-webkit-animation-name:fade-in;animation-name:fade-in;-webkit-animation-duration:500ms;animation-duration:500ms;z-index:1;cursor:pointer}#player-dropdown li{min-height:3vh;padding:2px 8px;border-bottom:1px solid yellow;display:flex;align-items:center;overflow:hidden;box-sizing:border-box}.player-names-container{width:75.5%;display:flex;background-color:rgba(0,0,0,0.5);color:white;align-items:center;padding:0 1vw;box-sizing:border-box;height:5vh;align-self:center;justify-content:space-evenly}@media only screen and (max-device-width: 500px){.player-names-container{font-size:10px}}footer{display:flex;flex-direction:column;height:4vh;background-color:indianred}
